<template>
  <div
    class="blog-category-container"
    v-loading="isLoading"
  >
    <h2>文章分类</h2>
    <RightList
      :listdata="list"
      @select="handleSelect"
    />
  </div>
</template>

<script>
import RightList from "./RightList";
import fetchData from "@/mixins/fetchData.js";
import { getBlogCategories } from "@/api/blog.js";
export default {
  mixins: [fetchData([])],
  components: {
    RightList,
  },
  data () {
    return {

    }
  },
  computed: {
    categoryId () {
      return +this.$route.params.categoryId || -1;
    },
    limit () {
      return +this.$route.query.limit || 10;
    },
    list () {
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount,
        0
      );

      let result = [
        { name: "全部", id: -1, articleCount: totalArticleCount }
      ];
      let replace = [];
      for (const iterator of this.data) {
        replace.push(iterator);
      }
      result = result.concat(replace).map((it) => ({
        ...it,
        isSelect: it.id === this.categoryId,
        aside: `${it.articleCount}篇`,
      }));
      return result;
    },
  },
  // mounted () {
  //   console.log(this.list);
  // },
  methods: {
    async fetchData () {
      const ass = await getBlogCategories();
      // console.log(ass);
      return ass;
    },
    handleSelect (item) {
      const query = {
        page: 1,
        limit: this.limit,
      };
      // 跳转到 当前的分类id  当前的页容量  newPage的页码
      if (item.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id,
            articleCount: item.articleCount,
          },
        });
      }
    },
  },
};
</script>

