    width: 100px;
    box-sizing: border-box;
    padding: 20px 8px;
    position: relative;
    height: 100%;
    overflow-y: auto;
    h2 {
      font-weight: bold;
      letter-spacing: 2px;
      font-size: 0.8em;
      margin: 0;
    }
  }
}
@media screen and (min-width: 768px) {
  .blog-category-container {
    width: 260px;
    box-sizing: border-box;
    padding: 20px;
    position: relative;
    height: 100%;
    overflow-y: auto;
    h2 {
      font-weight: bold;
      letter-spacing: 2px;
      font-size: 1em;
      margin: 0;
    }
  }
}
@media screen and (min-width: 992px) {
  .blog-category-container {
    width: 260px;
    box-sizing: border-box;
    padding: 20px;
    position: relative;
    height: 100%;
    overflow-y: auto;
    h2 {
      font-weight: bold;
      letter-spacing: 2px;
      font-size: 1em;
      margin: 0;
    }
  }
}
@media screen and (min-width: 1200px) {
  .blog-category-container {
    width: 280px;
