import getComponentRootDom from "./getComponentRootDom"
import Icon from "@/components/Icon"
import styles from "@/styles/message.module.less"
/**
 * 
 * @param {String} content 消息内容
 * @param {String} type 消息类型 success warn info error
 * @param {Number} duration 多久后消失
 * @param {HTMLElement} container 容器，消息会显示到该容器正中；如果不传，则显示在页面中间
 */
export default function (options = {}) {
  const content = options.content || "";
  const type = options.type || "info";
  const duration = options.duration || "2000";
  const container = options.container || document.body;
  const div = document.createElement("div");
  var iconDom;
  if (type === "info") {
    iconDom = "";
  } else {
    iconDom = getComponentRootDom(Icon, {
      type,
    });
  }
  if (iconDom === "") {
    div.innerHTML = `${content}`
  } else {
    div.innerHTML = `<span>${iconDom.outerHTML}</span><div>${content}</div>`
  }
  //设置样式
  const typeClassName = styles[`message-${type}`];
  div.className = `${styles.message} ${typeClassName}`;
  // 容器的position是否改动过
  if (options.container) {
    if (getComputedStyle(container).position === "static") {
      container.style.position = "relative";
    }
  }
  //将div加入到容器中
  container.appendChild(div);
  //浏览器强行渲染
  div.clientHeight;//导致reflow
  //回到正常位置
  div.style.opacity = 1;
  div.style.transform = `translate(-50%,-50%)`;
  setTimeout(() => {
    div.style.opacity = 0;
    div.style.transform = `translate(-50%,-50%) translateY(-25px)`;
    div.addEventListener(
      "transitionend",
      function () {
        div.remove();
        //运行回调函数
        options.callback && options.callback();
      },
      { once: true })
  }, duration)
}